        <h1 class="center">This is your daily minute!</h1>
      </div>
      <Newslist v-bind:duration="300" v-bind:articles="articles" v-bind:header="false"></Newslist>
    </div>
</template>

<script>
import Newslist from './Newslist'

export default {
  name: 'dailyminute',
  props: ['articles'],
  components: {
    Newslist
  },
  data () {
    return {
    }
  },
  mounted: function () {
  }
}
</script>

