<template>
  <div class="home">
    <DurationSelection v-on:durationChanged="durationChanged" v-on:topicChanged="topicChanged"></DurationSelection>
    <div v-if="duration">
      <Newslist v-bind:duration="duration" v-bind:articles="articles" v-bind:header="true"></Newslist>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Newslist from './Newslist'
import DurationSelection from './DurationSelection'

export default {
  name: 'home',
  props: ['articles'],
  components: {
    Newslist,
    DurationSelection
  },
  data () {
    return {
      duration: "",
      topic: ""
    }
  },
  methods: {
    durationChanged: function (duration) {
      this.duration = duration;
    },
    topicChanged: function (topic) {
      this.topic = topic;
    }
  },
  created: function () {
  }
}
</script>


<style>

</style>



