<template>
  <div class="about">
    <div class="jumbotron">
      <h1 class="center">DailyCommute</h1>
      <p class="lead center">Giving daily article suggestions, tailored to the duration of your commute. </p>
    </div>
    <div class="row">
      <div class="col-lg-4">
        <h3>Problem...</h3>
        <p>In the US alone, there are over 140 million workers commuting every workday. While commuting, I love reading all kinds of articles, but I've noticed that I often don't finish reading the article in time of arrival, which breaks the momentum and means that I probably won't go back to read the rest of it.</p>
      </div>
      <div class="col-lg-4">
        <h3>Idea</h3>
        <p>What if articles you read on commutes would be tailored to the duration of your commute? Allowing you to fully commit and actually finish reading the article in time.</p>
      </div>
      <div class="col-lg-4">
        <h3>Solution</h3>
        <p>DailyCommute asks for your commute duration and suggests you articles that are short enough for you to finish reading on your commute.</p>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'about'
}
</script>

<style>

</style>



